.@{css-prefix}timePicker{
	display: inline-block;
	line-height: normal;
	width: 168px;
	position: relative;
	left: 400px;
	top: 20px;
	&-rel{
		position: relative;
		&-wrapper{
			display: inline-block;
			width: 100%;
			position: relative;
			vertical-align: middle;
			line-height: normal;
		}
		&-icon{
			display: inline-block;
			font-family: Ionicons;
			speak: none;
			font-style: normal;
			font-weight: 400;
			font-variant: normal;
			text-transform: none;
			text-rendering: auto;
			-webkit-font-smoothing: antialiased;
			width: 32px;
			height: 32px;
			line-height: 32px;
			font-size: 16px;
			text-align: center;
			color: #80848f;
			position: absolute;
			right: 0;
			z-index: 3;
		}
		&-input{
			display: inline-block;
			width: 100%;
			height: 32px;
			line-height: 1.5;
			padding: 4px 7px;
			font-size: 12px;
			border: 1px solid #dddee1;
			border-radius: 4px;
			color: #495060;
			background-color: #fff;
			background-image: none;
			position: relative;
			cursor: text;
			transition: border .2s ease-in-out,background .2s ease-in-out,box-shadow .2s ease-in-out;
		}
	}

	&-dropdown{
		transform-origin: center top 0px;
		margin: 5px 0;
		padding: 0;
		overflow: visible;
		max-height: none;
		background-color: #fff;
		box-sizing: border-box;
		border-radius: 4px;
		box-shadow: 0 1px 6px rgba(0,0,0,.2);
		position: absolute;
		top: 32px;
		left: 0px;
		z-index: 900;
		display: none;
		&-rel{
			display: flex;
		}
		&-footer{
			width: 100%;
			height: 40px;
			border-top: 1px solid #e9eaec;
			display: flex;
			justify-content: flex-end;
			align-items: center;
			button{
				width: 40px;
				height: 26px;
				border: none;
				margin-right: 10px;
				transition: all .3s ease;
				cursor: pointer;
			}
		}
		&-btnl{
			background-color: white;
		}
		&-btnl:hover{
			color: skyblue;
		}
		&-btnr{
			background-color: skyblue;
			color: white;
		}
		&-main{
			
		}
		&-content{
			&-star{
				display: flex;
			}
			&-end{
				display: flex;
			}
		}
		&-ul{
			width: 55px;
			height: 144px;
			overflow: auto;
			border-left: 1px solid #e9eaec;
			border-right: 1px solid #e9eaec;
			li{
				height: 24px;
				width: 100%;
				text-align: center;
				line-height: 24px;
				transition: all .3s ease;
				cursor: pointer;
			}
			li:hover{
				background-color: skyblue;
			}
		}
		&-title{
			height: 32px;
			line-height: 32px;
			text-align: center;
			border-bottom: 1px solid #e9eaec;
		}
		::-webkit-scrollbar{
				display: none;
			}
	}
}
